@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;1,900&display=swap);
li, a , button{
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #edf0f1;
    text-decoration: none;
}

.header{
    display: flex;
    background: #E06C00;  background-repeat: no-repeat;
    justify-content: space-between;
    align-items: center;
    height: 110px;
    width: auto;
    padding: 30px 10%;
    margin-bottom: 20px;
    box-sizing: border-box;
}

.logo-header{
    cursor: pointer;
    height: 110px;

}

.nav__links{
    list-style: none;
    margin-top: 15px;
}

.nav__links li {
    display: inline-block;
    padding: 0 20px;
}

.nav__links li a {
    transition: all 10.3s ease 0s;
}

.nav__links li a:hover {
    color: #0088a9;
}

button{
    padding: 9px 25px;
    background-color: #C76000 ;
    border:none;
    border-radius: 50px;
    cursor: pointer;
    margin-right: 15px;
    transition: all 0.3 ease 0s;
}

button:hover{
    background-color: #EF7500;
}

@media (max-width: 810px) {
    .header{
        display: none;
    }
   
  }
.Home{
    position: relative;    
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 100vh;
    margin-top: -20px;
    font-size: 100px;
    font-family: 'Roboto', sans-serif;
    overflow: hidden; /* Hide scrollbars */
}

.Bienvenida{
    place-self: center;
}

.Bienvenida img{
    width: 700px;
}

.Bienvenida_imagen{
    object-fit: cover;
    width: 100%;
    height: 100%;
}


@media (max-width: 640px) {
    .Bienvenida img {
      width: 400px;
    }
    .Home{
        grid-template-columns: 1fr ;   
    }
    .Bienvenida_imagen{
        display: none;
    }
  }

  @media (max-width: 810px) {
    .Bienvenida img {
      width: 400px;
    }
    .Home{
        grid-template-columns: 1fr ;   
    }
    .Bienvenida_imagen{
        display: none;
    }
  }
Login {
	width: 100%;
	height: 100vh;
	display: grid;
	place-items: center;
}
.Login-container {
	display: grid;
	grid-template-rows: auto 1fr auto;
	width: 300px;
}
.logo {
	width: 150px;
	margin-bottom: 48px;
	justify-self: center;
	display: none;
}
.form {
	display: flex;
	flex-direction: column;
}
.form a {
	color: var(--hospital-green);
	font-size: var(--sm);
	text-align: center;
	text-decoration: none;
	margin-bottom: 52px;
}
.label {
	font-size: var(--sm);
	font-weight: bold;
	margin-bottom: 4px;
}
.input {
	background-color: var(--text-input-field);
	border: none;
	border-radius: 8px;
	height: 30px;
	font-size: var(--md);
	padding: 6px;
	margin-bottom: 12px;
}
.input-email {
	margin-bottom: 22px;
}
.primary-button {
	background-color: var(--hospital-green);
	border-radius: 8px;
	border: none;
	color: var(--white);
	width: 100%;
	cursor: pointer;
	font-size: var(--md);
	font-weight: bold;
	height: 50px;
}
.secondary-button {
	background-color: var(--white);
	border-radius: 8px;
	border: 1px solid var(--hospital-green);
	color: var(--hospital-green);
	width: 100%;
	cursor: pointer;
	font-size: var(--md);
	font-weight: bold;
	height: 50px;
}
.login-button {
	margin-top: 14px;
	margin-bottom: 30px;
}
@media (max-width: 640px) {
	.logo {
		display: block;
	}
}



body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  
  }
